
.art-horizontal {
    display: block;
    white-space: nowrap;

    .art-thumb {
        display: inline-block;
        padding-right: $base-padding;
    }
}
.art-vertical {
    display: block;

    .art-thumb {
        display: block;
        padding-bottom: $base-padding;
    }
}


.art-cover-container {
    position: relative;
    width: 100%;
    height: auto;
    max-width: 1200px;
}
